import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    score: 0,
    againUrl: '/conversion',
    errors: {
      '2': 0,
      '8': 0,
      '10': 0,
      '16': 0
    },
    questions: null,
    topics: null
  },
  mutations: {
    setquestions(state, q){
      state.questions = q
    },
    setErrors(state, error){
      state.errors[error] += 1
    }
  },
  actions: {

  }
})
